.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  background-color: #202638;
  background-image: url("../img/background/index-background-mobile.png");
  background-size: 100%;
}
@media (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/background/index-background-mobile@2x.png");
  }
}
.header--opened .header__wrapper {
  background-color: #283645;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
  background-image: none;
}
.header--closed .header__toggle::before {
  content: "";
  display: block;
  position: absolute;
  top: 20px;
  width: 50px;
  right: 20px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 11px 0 0 #ffffff, 0 22px 0 0 #ffffff;
  background-image: none;
}
.header--closed .header__toggle {
  display: block;
}
.header--closed .header__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 11px 0 0 #d22856, 0 22px 0 0 #d22856;
}
.header--closed .header__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 11px 0 0 rgba(210, 40, 86, 0.3), 0 22px 0 0 rgba(210, 40, 86, 0.3);
}
.header--opened .header__toggle {
  top: 0;
  right: auto;
  left: 0;
  display: block;
}
.header--opened .header__toggle::before,
.header--opened .header__toggle::after {
  content: "";
  position: absolute;
  display: block;
  top: 34px;
  width: 30px;
  right: 17px;
  height: 2px;
  background-color: #ffffff;
}
.header--opened .header__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header--opened .header__toggle::after {
  transform: rotate(-45deg);
}
.header--opened .header__toggle:hover::before,
.header--opened .header__toggle:hover::after {
  background-color: #d22856;
}
.header--opened .header__toggle:active::before,
.header--opened .header__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.header--nojs .header__toggle {
  display: none;
}
.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.3);
}
.header__logo {
  width: 74px;
  height: 23px;
  margin-right: auto;
  margin-left: 20px;
}
.header__pink-logo {
  fill: #ffffff;
}
.header__pink-logo:hover {
  fill: #d22856;
}
.header__pink-logo:active {
  fill: #ffffff;
  opacity: 0.3;
}
.header__pink-logo--tablet {
  display: none;
}
.header__pink-logo--desktop {
  display: none;
}
.header__toggle {
  display: none;
  width: 70px;
  height: 69px;
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
}
.header__toggle--opened {
  display: block;
}
@media (min-width: 660px) {
  .header {
    background-image: url("../img/background/index-background-tablet.png");
  }
  .header--opened {
    background-image: none;
  }
